/**
 * @module M/impl/Control/SharemapControl
 */

export default class SharemapControl extends M.impl.Control {
  /**
   * @classdesc
   * Main constructor of the sharemapControl.
   *
   * @constructor
   * @extends {M.impl.Control}
   * @api stable
   */
  constructor() {
    super();
    //Urls de los servicios de las capas geoJSON a compartir
    this.shareGeoJSONurls_ = [];
  }
  /**
   * This function adds the control to the specified map
   *
   * @public
   * @function
   * @param {M.Map} map to add the plugin
   * @param {HTMLElement} html of the plugin
   * @api stable
   */
  addTo(map, html) {
    super.addTo(map, html);
  }

  /**
   * This function 
   *
   * @public
   * @function
   * @param {layer} layer to encode
   * @api stable
   */
  encodeLayer(layer) {
    return (new Promise((success, fail) => {
      if (layer.type === M.layer.type.KML) {
        success(this.encodeKML(layer));
      } else if (layer.type === M.layer.type.WMS) {
        success(this.encodeWMS(layer));
      } else if (layer.type === M.layer.type.WFS) {
        success(this.encodeWFS(layer));
      } else if (layer.type === M.layer.type.WFST) {
        success(this.encodeWFS(layer));
      } else if(layer.type === M.layer.type.GeoJSON) {
        success(this.encodeGeoJSON(layer));
      } else if (layer.type === M.layer.type.WMTS) {
        success(this.encodeWMTS(layer));
      }
    }));
  }
  /**
   * This function encodes WMS layer
   *
   * @public
   * @function
   * @param {layer} WMS layer to encode
   * @api stable
   */
  encodeWMS(layer) {
    let encodedLayer = null;

    const layerImpl = layer.getImpl();
    const layerLegend = layerImpl.legend;
    const layerUrl = layerImpl.url;
    const layerName = layerImpl.name;
    const layerTransparent = layerImpl.transparent;
    const layerTiled = layerImpl.tiled;

    encodedLayer = {
      type: 'WMS',
      legend: layerLegend,
      url: layerUrl,
      name: layerName,
      transparent: layerTransparent,
      tiled: layer.tiled,
    };
    return encodedLayer;
  }
  /**
   * This function encodes WMTS layer
   *
   * @public
   * @function
   * @param {layer} WMTS layer to encode
   * @api stable
   */
  encodeWMTS(layer) {
    let encodedLayer = null;

    const layerImpl = layer.getImpl();
    const layerUrl = layerImpl.url;
    const layerName = layerImpl.name;
    const layerEPSG = layerImpl.matrixSet;
    const layerLegend = layerImpl.legend;

    encodedLayer = {
      type: 'WMTS',
      url: layerUrl,      
      name: layerName,
      matrixSet: layerEPSG,
      legend: layerLegend
    };
    return encodedLayer;
  }
  /**
   * This function encodes WFS layer
   *
   * @public
   * @function
   * @param {layer} WFS layer to encode
   * @api stable
   */
  encodeWFS(layer) {
    let encodedLayer = null;

    //Verificamos si ya se ha añadido la capa desde otra capa GeoJSON
    if(!this.shareGeoJSONurls_.includes(layer.getImpl().url+"/"+layer.getImpl().name)) {
      this.shareGeoJSONurls_.push(layer.getImpl().url+"/"+layer.getImpl().name);
      const layerImpl = layer.getImpl();
      const layerLegend = layerImpl.legend;
      const layerUrl = layerImpl.url;
      const layerVersion = layerImpl.version;
      let layerOutput = layer.options.getFeatureOutputFormat;
      if(M.utils.isUndefined(layerOutput) || M.utils.isNullOrEmpty(layerOutput)) {
        layerOutput = "application/json";
      }
      let layerName;
      if (M.utils.isUndefined(layerImpl.namespace)) {
        layerName = layerImpl.name;
      }else {
        layerName = layerImpl.namespace + ":" + layerImpl.name;
      }

      encodedLayer = {
        type: 'GeoJSON',
        legend: layerLegend,
        url: layerUrl + "service%3DWFS" + "%26version%3D" + layerVersion + "%26request%3DGetFeature" + "%26typename%3D" + layerName + "%26outputFormat%3D" + layerOutput
      };
    }

    return encodedLayer;
  }
  /**
   * This function encodes GeoJSON layer
   *
   * @public
   * @function
   * @param {layer} WFS layer to encode
   * @api stable
   */
  encodeGeoJSON(layer) {
    let encodedLayer = null;

    //Verificamos si ya se ha añadido la capa desde otra capa WFS
    if(!M.utils.isUndefined(layer.source.share)) {
      if(!this.shareGeoJSONurls_.includes(layer.source.share.url+"/"+layer.source.share.name)) {
        this.shareGeoJSONurls_.push(layer.source.share.url+"/"+layer.source.share.name);
        const layerLegend = layer.source.share.legend;
        const layerUrl = layer.source.share.url;
        const layerVersion = layer.source.share.version;
        let layerOutput = layer.source.share.getFeatureOutputFormat;
        if(M.utils.isUndefined(layerOutput) || M.utils.isNullOrEmpty(layerOutput)) {
          layerOutput = "application/json";
        }
        let layerName;
        if (M.utils.isUndefined(layer.source.share.namespace)) {
          layerName = layer.source.share.name;
        }else {
          layerName = layer.source.share.namespace + ":" + layer.source.share.name;
        }

        encodedLayer = {
          type: 'GeoJSON',
          legend: layerLegend,
          url: layerUrl + "service=WFS" + "%26version=" + layerVersion + "%26request=GetFeature" + "%26typename=" + layerName + "%26outputFormat%3D" + layerOutput
        };
      }      
    }
    return encodedLayer;
  }
  /**
   * This function encodes KML layer
   *
   * @public
   * @function
   * @param {layer} WFS layer to encode
   * @api stable
   */
  encodeKML(layer) {
    let encodedLayer = null;

    const layerImpl = layer.getImpl();
    const layerLegend = layerImpl.name;
    const layerUrl = layerImpl.url;
    const layerExtract = layerImpl.extract;

    encodedLayer = {
      type: 'KML',
      legend: layerLegend,
      url: layerUrl,
      extract: layerExtract
    };
    return encodedLayer;
  }
}