/** 
 * @module M/control/SharemapControl
 */

import SharemapImplControl from 'impl/sharemapcontrol';
import template from 'templates/sharemap';
import templateDialog from 'templates/sharemapdialog';

export default class SharemapControl extends M.Control {

  /**
   * @classdesc
   * Main constructor of the class. Creates a PluginControl
   * control
   *
   * @constructor
   * @extends {M.Control}
   * @api stable
   */
  constructor(urlmapea, controls, wmcfile) {
    // 1. checks if the implementation can create PluginControl
    if (M.utils.isUndefined(SharemapControl)) {
      M.exception('La implementación usada no puede crear controles SharemapControl');
    }
    // 2. checks the mandatory parameter urlmapea
    if (M.utils.isUndefined(urlmapea)) {
      M.dialog.error('Falta el parámetro correspondiente a la url de mapea');
      M.exception('La implementación usada no puede crear controles SharemapControl. Falta el parámetro urlmapea');
    }
    // 3. implementation of this control
    let impl = new SharemapImplControl();
    super(impl, "sharemap");

    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.facadeMap_ = null;

    /**
     * Mapea url
     * @private
     * @type {M.Map}
     */
    this.urlmapea_ = urlmapea;

    /**
     * List of controls
     * @private
     * @type {M.Map}
     */
    this.urlcontrols_ = controls;

    /**
     * List of WMC
     * @private
     * @type {M.Map}
     */
    this.urlwmcfile_ = wmcfile;



    if (!M.template.compileSync){
      M.template.compileSync = (string, options) => {
        let templateCompiled;
        let templateVars = {};
        let parseToHtml;
        if (!M.utils.isUndefined(options)){
          templateVars = M.utils.extends(templateVars, options.vars);
          parseToHtml = options.parseToHtml;
        }
        const templateFn = Handlebars.compile(string);
        const htmlText = templateFn(templateVars);
        if (parseToHtml !== false) {
          templateCompiled = M.utils.stringToHtml(htmlText);
        } else {
          templateCompiled = htmlText;
        }
        return templateCompiled;
      };
    }
  }
  /**
   * This function creates the view
   *
   * @public
   * @function
   * @param {M.Map} map to add the control
   * @api stable
   */
   createView(map) {
     this.facadeMap_ = map;
     return new Promise((success, fail) => {
      const html = M.template.compileSync(template);
      this.addEvents(html);
      success(html);
    });
   }
   /**
   * This function creates the view to the specified map
   *
   * @public
   * @function
   * @param {M.Map} map to add the control
   * @api stable
   */
  addEvents(html) {
    this.element_ = html;

    // Share button
    const shareBtn = this.element_.querySelector('button.g-cartografia-share');
    shareBtn.addEventListener('click', this.shareClick.bind(this));
  }
   /**
   * This function gets activation button
   *
   * @public
   * @function
   * @param {HTML} html of control
   * @api stable
   */
  // getActivationButton(html) {
  //   return html.querySelector('.m-sharemap button');
  // };
  
  /**
   * This function compares controls
   *
   * @public
   * @function
   * @param {M.Control} control to compare
   * @api stable
   */
  equals(control) {
    return control instanceof SharemapControl;
  }

  /**
   * This function makes the url to share
   *
   * @private
   * @function
   */
  shareClick(evt) {
    evt.preventDefault();
    this.getImpl().shareGeoJSONurls_ = [];

    this.getshareData().then((shareData) => {
      let urlRest = this.urlmapea_.charAt(this.urlmapea_.length-1) != "?" ? this.urlmapea_ += "?" : this.urlmapea_;
      urlRest += "layers="+shareData.layers+"&bbox="+shareData.bbox.x.min+","+shareData.bbox.y.min+","+shareData.bbox.x.max+","+shareData.bbox.y.max+"&projection="+shareData.projection+"*m";    
      if(!M.utils.isNullOrEmpty(shareData.wmcfile) && !M.utils.isUndefined(shareData.wmcfile)) urlRest += "&wmcfile="+shareData.wmcfile;
      if(!M.utils.isNullOrEmpty(shareData.controls) && !M.utils.isUndefined(shareData.controls)) urlRest += "&controls="+shareData.controls;

      let url = {
        url: urlRest
      }; 
      const options = { jsonp: true, vars: url };
      const html = M.template.compileSync(templateDialog, options);
      M.dialog.show(html.innerHTML, "Compartir mapa", 'info').then(() => {
        let textArea = document.getElementById("m-sharemap-shareUrl");
        SharemapControl.resizeTextArea(textArea);

        //Copy button
        var copyBtn = document.createElement("BUTTON");
        copyBtn.innerText = "Copiar";
        document.getElementsByClassName("m-button")[0].appendChild(copyBtn);

        //Event
        copyBtn.addEventListener('click', this.copyText);
      });
    });
  }

  /**
   * This function returns map layers to share
   *
   * @private
   * @function
   */
  getshareData(evt) {
    const mapZoom = this.facadeMap_.getZoom();
    const mapProjection = this.facadeMap_.getProjection();
    const mapCenter = this.facadeMap_.getCenter();
    const mapBbox = this.facadeMap_.getBbox();
    const mapMaxExtent = this.facadeMap_.getMaxExtent();
    const mapControls = this.urlcontrols_;

    let layers = new Array();

    const shareData = {
      zoom: mapZoom,
      center: mapCenter,     
      bbox: mapBbox,
      maxextent: mapMaxExtent,
      controls: mapControls,
      projection: mapProjection.code,
      wmcfile: this.urlwmcfile_
    };
    return this.encodeLayers().then((encodedLayers) => {
      encodedLayers.forEach((encodedLayer) => {
        let encodedLayerArray = Object.values(encodedLayer);
        layers.push(encodedLayerArray.join('*'));
      });

      shareData.layers = layers.join(',');
      return shareData;
    });
  }
  /**
   * This function returns encoded layers
   *
   * @private
   * @function
   */
  encodeLayers() {
    const layers = this.facadeMap_.getLayers().filter((layer) => {
      let context = false;
      let wmcArray = this.facadeMap_.getWMC();
      //No se incluyen las capas que van implícitas en el contexto pasado como parámetro
      wmcArray.forEach((wmc) => {
        let WMCFileName = wmc.url.substring(wmc.url.lastIndexOf("/"));
        if(this.urlwmcfile_.search(WMCFileName) != -1 && wmc.layers.includes(layer)) context = true;
      });
      return ((layer.isVisible() === true) && (layer.inRange() === true) && !context);
    });

    return (new Promise((success, fail) => {
      const encodedLayers = [];
      layers.forEach((layer) => {
        this.getImpl().encodeLayer(layer).then((encodedLayer) => {
          if (!M.utils.isNullOrEmpty(encodedLayer)) {
            encodedLayers.push(encodedLayer);
          }
        });
      });
      success(encodedLayers);
    }));
  }

  /**
   * This function copy url to clipboard
   *
   * @private
   * @function
   */
  copyText() {
    let codigoACopiar = document.getElementById('m-sharemap-shareUrl');
    let alertDiv = document.getElementById('m-sharemap-copialert');
    window.getSelection().removeAllRanges();
    codigoACopiar.select();
    let mensaje = "";    
    try {
      let copiado = document.execCommand('copy');
      if (copiado){
        document.getElementsByClassName("m-button")[0].classList.add(SharemapControl.COPIED_CLASS);
        document.getElementsByClassName("m-button")[0].classList.remove(SharemapControl.NOTCOPIED_CLASS);
        mensaje = SharemapControl.COPIED_MSG;
      }else{
        document.getElementsByClassName("m-button")[0].classList.add(SharemapControl.NOTCOPIED_CLASS);
        document.getElementsByClassName("m-button")[0].classList.remove(SharemapControl.COPIED_CLASS);
        mensaje = SharemapControl.NOTCOPIED_CLASS;
      }
    }catch(ex) {
      document.getElementsByClassName("m-button")[0].classList.add(SharemapControl.NOTCOPIED_CLASS);
      document.getElementsByClassName("m-button")[0].classList.remove(SharemapControl.COPIED_CLASS);
      mensaje = SharemapControl.NOTCOPIED_CLASS;
    }

    if(document.getElementsByClassName("m-button")[0].childNodes[0].textContent.trim() == ''){
      var text = document.createTextNode(mensaje);
      document.getElementsByClassName("m-button")[0].insertBefore(text, document.getElementsByClassName("m-button")[0].firstChild);
      setTimeout(function() {
        text.parentNode.removeChild(text);
      }, 1500);
    }
    window.getSelection().removeAllRanges();
  }
}

/**
 * Template for show dialog
 *
 * @const
 * @type {string}
 * @public
 * @api stable
 */
SharemapControl.DIALOG_TEMPLATE = 'sharemapdialog.html';

/**
 * Class 'copied'
 * @const
 * @type {string}
 * @public
 * @api stable
 */
SharemapControl.COPIED_CLASS = 'copied';

/**
 * Class 'notcopied'
 * @const
 * @type {string}
 * @public
 * @api stable
 */
SharemapControl.NOTCOPIED_CLASS = 'notcopied';

/**
 * String for copied message 
 *
 * @const
 * @type {string}
 * @public
 * @api stable
 */
SharemapControl.COPIED_MSG = 'URL copiada al portapapeles';

/**
 * String for not copied message 
 *
 * @const
 * @type {string}
 * @public
 * @api stable
 */
SharemapControl.NOTCOPIED_MSG = 'Error copiando la URL';

SharemapControl.resizeTextArea = function(element) {
  let height = (element.scrollHeight / document.documentElement.clientHeight) * 100;
  element.style.height = 'auto';
  //Max height
  const maxHeight = 30;
  if(height < maxHeight) {     
    element.style.height = height + 'vh';
  }else{
    element.style.height = maxHeight + 'vh';
  }
};

SharemapControl.transformMaxExtent = function(maxextent) {
  if(maxextent instanceof Array){
    return maxextent.join(',');
  }else{
    return [maxextent.x.min,maxextent.y.min,maxextent.x.max,maxextent.y.max].join(',');
  }
};