/**
 * @module M/plugin/Sharemap
 */
import 'assets/css/sharemap.css';
import SharemapControl from './sharemapcontrol.js';

export default class Sharemap extends M.Plugin {

  /**
   * @classdesc
   * Main facade plugin object. This class creates a plugin
   * object which has an implementation Object
   *
   * @constructor
   * @extends {M.Plugin}
   * @param {Object} impl implementation object
   * @api stable
   */
  constructor(params) {

    super();
    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.map_ = null;

    /**
     * Array of controls
     * @private
     * @type {Array<M.Control>}
     */
    this.controls_ = [];

    this.options_ = params.options || {};
    this.urlmapea_ = this.options_.urlmapea;
    this.urlcontrols_ = this.options_.urlcontrols;
    this.panelPosition_ = (((!M.utils.isUndefined(this.options_.panel)) && ((!M.utils.isNullOrEmpty(this.options_.panel.position)) || (!M.utils.isUndefined(this.options_.panel.position)))) ? this.options_.panel.position : M.ui.position.TR);
    this.urlwmcfile_ = this.options_.urlwmcfile;
  }

  /**
   * This function adds this plugin into the map
   *
   * @public
   * @function
   * @param {M.Map} map the map to add the plugin
   * @api stable
   */
  addTo(map) {
    this.controls_.push(new SharemapControl(this.urlmapea_, this.urlcontrols_, this.urlwmcfile_));
    this.map_ = map;
    this.panel_ = new M.ui.Panel("panelsharemap", {
      collapsible: false,
      position: this.panelPosition_,
      collapsedButtonClass: "g-cartografia-flecha-izquierda",
      tooltip: "Compartir mapa"
    });
    this.panel_.addControls(this.controls_);
    map.addPanels(this.panel_);
  }
}
